.tarefas{
  margin-top: 30px;

}

.tarefas li {
  display: -ms-flexbox;
  display: flex;
   -ms-flex-align: center;
       align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding: 10px;
}
.tarefas li:hover {
  background-color: #51c5de;
}
.tarefas li .edit{
  margin-right: 15px;
  color: #51c5de;
}
.tarefas li .delete{
  margin-right: 15px;
  color: #f04c64;
}

.tarefas li:hover .edit,
.tarefas li:hover .delete{
  color: #0a4f70;
}

.tarefas li .edit,
.tarefas li .delete{
  cursor: pointer;
  -webkit-transition: all 600ms;
  -o-transition: all 600ms;
  transition: all 600ms;
}

.tarefas li span{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}



